<template>
	<el-dialog v-model="formVisible" :title="'考卷'" width="70%" destroy-on-close>
		<div class="list_view">
			<el-form :model="searchQuery" class="list_search">
				<div class="search_view">
					<div class="search_label">
						试题名称：
					</div>
					<div class="search_box">
						<el-input class="search_inp" v-model="searchQuery.questionname" placeholder="试题名称"
							style="100%" size="small" clearable>
						</el-input>
					</div>
				</div>
				<div class="search_btn_view">
					<el-button class="search_btn" type="primary" @click="searchClick">搜索</el-button>
				</div>
			</el-form>
			<div class="list_bottom">
				<div class="data_box">
					<div class="data_view">
						<el-table v-loading="listLoading" border :stripe='false' ref="table" :data="list">
							<el-table-column label="序号" width="120"  :resizable='true' align="left" header-align="left">
								<template #default="scope">{{ scope.$index + 1}}</template>
							</el-table-column>
							<el-table-column label="考生姓名" :resizable='true' align="left" header-align="left">
								<template #default="scope">
									{{scope.row.username}}
								</template>
							</el-table-column>
							<el-table-column label="在线考试名称" :resizable='true' align="left" header-align="left">
								<template #default="scope">
									{{scope.row.papername}}
								</template>
							</el-table-column>
							<el-table-column label="试题名称" :resizable='true' align="left" header-align="left">
								<template #default="scope">
									{{scope.row.questionname}}
								</template>
							</el-table-column>
							<el-table-column label="分值" :resizable='true' align="left" header-align="left">
								<template #default="scope">
									{{scope.row.score}}
								</template>
							</el-table-column>
							<el-table-column label="正确答案" :resizable='true' align="left" header-align="left">
								<template #default="scope">
									{{scope.row.answer}}
								</template>
							</el-table-column>
							<el-table-column label="考试得分" :resizable='true' align="left" header-align="left">
								<template #default="scope">
									<el-tag type="success" v-if="scope.row.myscore>0">{{scope.row.myscore}}</el-tag>
									<el-tag type="danger" v-else>{{scope.row.myscore}}</el-tag>
								</template>
							</el-table-column>
							<el-table-column label="考生答案" :resizable='true' align="left" header-align="left">
								<template #default="scope">
									{{scope.row.myanswer}}
								</template>
							</el-table-column>
							<el-table-column label="考试时间" :resizable='true' align="left" header-align="left">
								<template #default="scope">
									{{scope.row.addtime}}
								</template>
							</el-table-column>
						</el-table>
					</div>
					<el-pagination
						background 
						:layout="layouts.join(',')"
						:total="total" 
						:page-size="listQuery.limit"
						prev-text="<"
						next-text=">"
						:hide-on-single-page="false"
						:style='{"border":"0px solid #eee","padding":"4px 0","margin":"10px 0 20px","whiteSpace":"nowrap","color":"#333","textAlign":"center","flexWrap":"wrap","background":"none","display":"flex","width":"100%","fontWeight":"500","justifyContent":"center"}'
						@size-change="sizeChange"
						@current-change="currentChange" 
						@prev-click="prevClick"
						@next-click="nextClick"  />
				</div>
			</div>
		</div>
		
	</el-dialog>
</template>

<script setup>
	import {
		ref,
		nextTick,
		getCurrentInstance
	} from 'vue';
	import {
		useRoute,
		useRouter
	} from 'vue-router'
	import {
		ElMessageBox
	} from 'element-plus'
	const formVisible = ref(false)
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	//基础信息
	const tableName = 'examrecord'
	const formName = '考试记录'
	const route = useRoute()
	//基础信息
	const listLoading = ref(false)
	const list = ref([])
	//分页
	const layouts = ref(["prev","pager","next","sizes"])
	const total = ref(0)
	const sizeChange = (size) => {
		listQuery.value.limit = size
		getList()
	}
	const currentChange = (page) => {
		listQuery.value.page = page
		getList()
	}
	const prevClick = () => {
		listQuery.value.page = listQuery.value.page - 1
		getList()
	}
	const nextClick = () => {
		listQuery.value.page = listQuery.value.page + 1
		getList()
	}
	//分页
	//搜索
	const searchClick = () => {
		listQuery.value.page = 1
		getList()
	}
	const table = ref(null)
	const listChange = (row) => {
		nextTick(() => {
			table.value.clearSelection()
			table.value.toggleRowSelection(row)
		})
	}
	const listQuery = ref({
		page: 1,
		limit: 20,
	})
	const searchQuery = ref({})
	const selRows = ref([])
	//多选
	const handleSelectionChange = (e) => {
		selRows.value = e
	}
	//列表
	const getList = () => {
		listLoading.value = true
		let params = JSON.parse(JSON.stringify(listQuery.value))
		if (searchQuery.value.questionname && searchQuery.value.questionname != '') {
			params['questionname'] = '%' + searchQuery.value.questionname + '%'
		}
		if(paperid.value){
			params['paperid'] = paperid.value
		}
		if(userid.value){
			params['userid'] = userid.value
		}
		context?.$http({
			url: `examrecord/page`,
			method: 'get',
			params: params
		}).then(res => {
			listLoading.value = false
			list.value = res.data.data.list
			total.value = Number(res.data.data.total)
		})
	}
	const formRef = ref(null)
	const formModelChange = () => {
		searchClick()
	}
	const infoClick = (row) => {
		nextTick(()=>{
			formRef.value.init(row)
		})
	}
	const paperid = ref(0)
	const userid = ref(0)
	const init = (row) => {
		paperid.value = row.paperid
		userid.value = row.userid
		getList()
		formVisible.value = true
	}
	//声明父级调用
	defineExpose({
		init
	})
	
</script>

<style lang="scss" scoped>
	.list_view {
		border-radius: 20px;
		padding: 0;
		margin: 0 auto;
		overflow: hidden;
		background: #fff;
		width: 64%;
		//搜索
		.list_search {
			border: 0px solid #ddd;
			padding: 20px 0 0;
			margin: 0px auto;
			background: #fff;
			display: flex;
			justify-content: center;
			align-items: center;
			.search_view {
				margin: 0 10px 0 0;
				display: flex;
				align-items: center;
				.search_label {
					margin: 0 10px 0 0;
					color: #fff;
					font-weight: 500;
					display: inline-block;
					font-size: 14px;
					line-height: 40px;
					height: 40px;
				}
				.search_box {
					display: flex;
					width: calc(100% - 100px);
					// 输入框
					:deep(.search_inp) {
						border: 1px solid #eee;
						border-radius: 20px;
						box-shadow: 0 0 0px rgba(85, 255, 255, 0.5);
						padding: 0 10px;
						outline: 0px solid #efefef;
						background: #fff;
						width: 100%;
						outline-offset: 0px;
						line-height: 36px;
						box-sizing: border-box;
						min-width: 200px;
						height: 36px;
						//去掉默认样式
						.el-input__wrapper{
							border: none;
							box-shadow: none;
							background: none;
							border-radius: 0;
							height: 100%;
							padding: 0;
						}
						.is-focus {
							box-shadow: none !important;
						}
					}
				}
			}
			.search_btn_view {
				margin: 20px 0;
				display: flex;
				// 搜索按钮
				.search_btn {
					border: 0;
					cursor: pointer;
					border-radius: 20px;
					padding: 0 20px;
					outline: none;
					margin: 0 10px 0 0;
					color: #fff;
					background: #0099ff;
					width: auto;
					font-size: 14px;
					height: 36px;
				}
				// 搜索按钮-悬浮
				.search_btn:hover {
					opacity: 0.8;
				}
			}
		}
	}
	// 数据盒子
	.list_bottom {
		border-radius: 0 0 20px 20px;
		padding: 0 20px 20px;
		background: #fff;
		display: flex;
		width: calc(100% - 0px);
		align-items: flex-start;
		flex-wrap: wrap;
		//列表
		.data_box {
			border: 0px solid #ddd;
			padding: 0;
			background: none;
			width: 100%;
			.data_view {
				border: 0px solid #eee;
				padding: 0px;
				overflow: hidden;
				background: none;
				display: flex;
				width: 100%;
				flex-wrap: wrap;
				// 表格样式
				.el-table {
					padding: 0;
					background: #fff;
					width: 100%;
					border-color: #eee;
					border-width: 1px 0 0 1px;
					border-style: solid;
					:deep(.el-table__header-wrapper) {
						thead {
							color: #999;
							background: #f6f6f6;
							font-weight: 500;
							width: 100%;
							tr {
								background: none;
								th {
									padding: 12px 0;
									background: none;
									border-color: #eee;
									border-width: 0 1px 1px 0;
									border-style: solid;
									text-align: left;
									.cell {
										padding: 0 10px;
										word-wrap: normal;
										color: #666;
										word-break: break-all;
										white-space: normal;
										font-weight: bold;
										display: inline-block;
										vertical-align: middle;
										width: 100%;
										line-height: 24px;
										position: relative;
										text-overflow: ellipsis;
										//未选中样式
										.el-checkbox {
											//复选框
											.el-checkbox__inner {
												background: #fff;
												border-color: #ccc;
											}
										}
										//选中样式
										.is-checked {
											//复选框
											.el-checkbox__inner {
												background: #55ffff;
												border-color: #55ffff;
											}
										}
									}
								}
							}
						}
					}
					:deep(.el-table__body-wrapper) {
						tbody {
							width: 100%;
							tr {
								background: #fff;
								td {
									padding: 12px 0;
									color: #666;
									background: #fff;
									border-color: #eee;
									border-width: 0 1px 1px 0;
									border-style: solid;
									text-align: left;
									.cell {
										padding: 0 10px;
										overflow: hidden;
										word-break: break-all;
										white-space: normal;
										line-height: 24px;
										text-overflow: ellipsis;
										//未选中样式
										.el-checkbox {
											//复选框
											.el-checkbox__inner {
												background: #fff;
												border-color: #ccc;
											}
										}
										//选中样式
										.is-checked {
											//复选框
											.el-checkbox__inner {
												background: #55ffff;
												border-color: #55ffff;
											}
										}
									}
								}
							}
							tr:hover {
								td {
									padding: 12px 0;
									color: #333;
									background: #f8f8f8;
									border-color: #eee;
									border-width: 0 1px 1px 0;
									border-style: solid;
									text-align: left;
								}
							}
						}
					}
				}
			}
		}
	}
	// 分页器
	.el-pagination {
		// 总页码
		:deep(.el-pagination__total) {
			margin: 0 10px 0 0;
			color: #666;
			font-weight: 400;
			display: inline-block;
			vertical-align: top;
			font-size: 13px;
			line-height: 24px;
			height: 24px;
		}
		// 上一页
		:deep(.btn-prev) {
			border: none;
			border-radius: 2px;
			padding: 0;
			margin: 0 2px;
			color: #666;
			background: #f4f4f5;
			display: inline-block;
			vertical-align: top;
			font-size: 13px;
			line-height: 24px;
			min-width: 24px;
			height: 24px;
		}
		// 下一页
		:deep(.btn-next) {
			border: none;
			border-radius: 2px;
			padding: 0;
			margin: 0 2px;
			color: #666;
			background: #f4f4f5;
			display: inline-block;
			vertical-align: top;
			font-size: 13px;
			line-height: 24px;
			min-width: 24px;
			height: 24px;
		}
		// 上一页禁用
		:deep(.btn-prev:disabled) {
			border: none;
			cursor: not-allowed;
			padding: 0;
			margin: 0 2px;
			color: #C0C4CC;
			display: inline-block;
			vertical-align: top;
			font-size: 13px;
			line-height: 24px;
			border-radius: 2px;
			background: #f4f4f5;
			width: 24px;
			height: 24px;
		}
		// 下一页禁用
		:deep(.btn-next:disabled) {
			border: none;
			cursor: not-allowed;
			padding: 0;
			margin: 0 2px;
			color: #C0C4CC;
			display: inline-block;
			vertical-align: top;
			font-size: 13px;
			line-height: 24px;
			border-radius: 2px;
			background: #f4f4f5;
			width: 24px;
			height: 24px;
		}
		// 页码
		:deep(.el-pager) {
			padding: 0;
			margin: 0;
			display: inline-block;
			vertical-align: top;
			// 数字
			.number {
				cursor: pointer;
				padding: 0 4px;
				margin: 0 5px;
				color: #666;
				display: inline-block;
				vertical-align: top;
				font-size: 13px;
				line-height: 24px;
				border-radius: 2px;
				background: #f4f4f5;
				text-align: center;
				min-width: 24px;
				height: 24px;
			}
			// 数字悬浮
			.number:hover {
				cursor: pointer;
				padding: 0 4px;
				margin: 0 5px;
				color: #fff;
				display: inline-block;
				vertical-align: top;
				font-size: 13px;
				line-height: 24px;
				border-radius: 2px;
				background: #0099ff;
				text-align: center;
				min-width: 24px;
				height: 24px;
			}
			// 选中
			.number.is-active {
				cursor: default;
				padding: 0 4px;
				margin: 0 5px;
				color: #fff;
				display: inline-block;
				vertical-align: top;
				font-size: 13px;
				line-height: 24px;
				border-radius: 2px;
				background: #0099ff;
				text-align: center;
				min-width: 24px;
				height: 24px;
			}
		}
		// sizes
		:deep(.el-pagination__sizes) {
			box-shadow: none;
			margin: 0 0 0 5px;
			display: inline-block;
			vertical-align: top;
			font-size: 13px;
			line-height: 24px;
			height: 24px;
			.el-select {
				border: 0px solid #DCDFE6;
				cursor: pointer;
				padding: 0;
